body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  background: url(images/star.svg) 50% no-repeat ;
}

.stars--1 :nth-child(-n +1),
.stars--2 :nth-child(-n +2),
.stars--3 :nth-child(-n +3),
.stars--4 :nth-child(-n +4),
.stars--5 :nth-child(-n +5) {
  background: url(images/star-active.svg) 50% no-repeat;
}

.stars :not(:last-child) {
  margin-right: 4px;
}
